<template>
  <input type="file" accept="image/png, image/jpeg, image/gif"
    :multiple="multiple ? 'multiple' : false"
    id="uploader"
    style="display: none"
    @change="handleUpload">
</template>

<script>
export default {
  data () {
    return {
      multiple: false, // 
      uploader: null, // input file
      cb: null // 
    }
  },

  computed: {
    // 
    top () {
      return this.$store.state.top
    }
  },

  mounted () {
    this.uploader = document.getElementById('uploader')

    /**
     * 
     * 
     *
     * 
     * $communicator.$emit('upload', function (payload) {/.../})
     * @param payload { Array } 
     * @param multiple { Boolean }  false
     */
    window.$communicator.$on('upload', (cb, multiple) => {
      this.multiple = !!multiple
      this.cb = cb
      setTimeout(() => {
        this.uploader.click()
      }, 0)
    })
  },

  methods: {
    // change
    handleUpload () {
      var files = this.uploader.files

      if (!files || files.length === 0) return

      files = Array.prototype.slice.call(this.uploader.files)

      // 
      // $.post('', files, () => {

      // })

      // 
      new Promise(resolve => {
        this.handleLoadQueue(resolve, files)()
      }).then(payload => {
        this.cb(payload)
      })
    },

    /**
     * 
     * 
     */
    handleLoadQueue (resolve, files) {
      var i = 0
      var len = files.length
      var payload = []

      var download = () => {
        // files 
        // url = files[i]
        var url = window.URL.createObjectURL(files[i])

        new Promise(resolve => {
          this.getImageWidth(url, resolve)
        }).then(size => {
          payload.push({
            width: size.w,
            height: size.h,
            top: this.top,
            url: url, // 
            src: 'images/' + files[i].name // 
          })

          // 
          if (++i === len) {
            resolve(payload)
          } else {
            download()
          }
        })
      }

      return download
    },

    /**
     *  new Image 
     *  promise
     *
     * @param url { URL | base64 }  url
     * @param res { Promise resolve }
     *
     * @return { Object } 
     */
    getImageWidth (url, res) {
      var img = new Image()
      img.src = url
      img.onload = function () {
        res({
          w: Math.round(img.width),
          h: Math.round(img.height)
        })
      }
    }
  }
}
</script>



